body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  font: 0.875em "Century Gothic", Futura, sans-serif;
  background: aliceblue;
}

ol, ul {
  padding-left: 1.875em;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#loadOverlay{display: none;}
.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@media (min-width: 768px) {
    .bd-placeholder-img-lg {
        font-size: 3.5rem;
    }
}

.first-home-row {
    margin-top: 20%;
}
.btn-circle {
    border-radius: 25%;
}

.box-connect-four {
    border: 0.1em solid darkblue;
    float: left;
    font-size: 7vmin;
    font-weight: bold;
    line-height: 3%;
    height: 9vmin;
    margin-right: -0.1%;
    margin-top: -0.1%;
    padding: 0;
    text-align: center;
    width: 8.9vmin;
    background: transparent;

}

.box-connect-four:focus {
    outline: none;
}

.kbd-navigation .box-connect-four:focus {
    background: #ddd;
}

.body-connect-four {
    margin-left: 30%;
}

.page-title-connect-four {
    margin-left: 37%;
}

.btn-group-nextprevious {
    margin-left: 34%;
}
.status {
    margin-top: 6%;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.box-tic-tac {
    float: left;
    font-size: 4em;
    font-weight: bold;
    line-height: 0.6em;
    height: 1.2em;
    margin-right: -0.02em;
    margin-top: -0.02em;
    padding: 0;
    text-align: center;
    width: 1.2em;
    color: white;
    background: transparent;
}

.kbd-navigation .box:focus {
    background: #ddd;
}

.blackboard {
    height: 100%;
    width: 100%;
    background: black;
    border: 10px solid #5b3a29;
}

.tic-tac-buttons {
    margin-top: 2%;
    margin-bottom: 5%;
}

.body-tic-tac {
    color: white;
    margin-left: 37%;
    margin-top: 10.5%;
    font-family: "Comic Sans MS";
}

.pagetitle-tic-tac {
    margin-top: 2%;
    margin-left: 35%;
    color: white;
    font-family: "Comic Sans MS";
}

.square-number input{
    background: transparent;
    color: white;
    margin-left: 17%;
    width: 2%;
    border-top: black;
    border-left: black;
    border-right: black;
    outline: none;
}
.square-number {
    margin-left: 0%;
}

.personal-outline-danger {
    color: red;
    background: transparent;
    border: 0.1em solid red;
}

.personal-outline-primary {
    color: #157DEC;
    background: transparent;
    border: 0.1em solid #157DEC;
}

.personal-outline-danger:hover {
    color: red;
}

.personal-outline-primary:hover {
    color: #157DEC;
}

.box-tic-tac:focus {
    outline: none;
    border-style: outset;
}

.container{
    margin-top: 2%;
}
